import React, { Component } from 'react';
import './App.css';
import { Switch, Route, withRouter } from 'react-router-dom';
import TagManager from 'react-gtm-module';

//Pages component
import NoMatch from './pages/NoMatch';
import MainPage from './pages/MainPage';
import UslugiPage from './pages/UslugiPage';
import SpecialistsPage from './pages/SpecialistsPage';
import SecurityPage from './pages/SecurityPage';
import DocumentsPage from './pages/DocumentsPage';
import FaqPage from './pages/FaqPage';
import FaqArticlePage from './pages/FaqArticlePage';
import FaqQuestionsPage from './pages/FaqQuestionsPage';
import FaqSectionPage from './pages/FaqSectionPage';

// Containers
import FaqPageContainer from '../containers/FaqPageContainer';
import FaqSectionPageContainer from '../containers/FaqSectionPageContainer';
import FaqQuestionsPageContainer from '../containers/FaqQuestionsPageContainer';
import FaqArticlePageContainer from '../containers/FaqArticlePageContainer';

const tagManagerArgs = {
  gtmId: 'GTM-000000',
  dataLayerName: 'PageDataLayer'
}

class App extends Component {
  componentDidMount() {
    TagManager.initialize(tagManagerArgs);
  }
  render() {
    return (
      <Switch>
        <Route path="/" exact component={MainPage} />
        <Route path="/uslugi" exact component={UslugiPage} />
        <Route path="/specialists" exact component={SpecialistsPage} />
        <Route path="/security" exact component={SecurityPage} />
        <Route path="/documents" exact component={DocumentsPage} />
        <Route path="/faq" exact component={FaqPageContainer} />
        <Route path="/faq/:section" exact component={FaqSectionPageContainer} />
        <Route path="/faq/:section/:sub_section" exact component={FaqQuestionsPageContainer} />
        <Route path="/faq/:section/:sub_section/:article" exact component={FaqArticlePageContainer} />
        <Route component={NoMatch} />
      </Switch>
    );
  }
}

export default withRouter(App)



