const API_URL = 'http://test8.fln.com.ua/wp-json/v3/';

export const client = (variables, query) => {
  if(variables) {
    return fetch(API_URL + query.urlParams,
      {
        method: query.method,
        headers: query.headers,
        body: variables ? JSON.stringify(variables) : null
      })
      .then(res => {
        if (res.status !== 200) {
          throw Error(res._bodyText);
        } else {
          return res.json()
        }
      })
      .catch(err => {
        throw err
      })
  }

  return fetch(API_URL + query.urlParams,
    {
      method: query.method,
      headers: query.headers
    })
    .then(res => {
      if (res.status !== 200) {
        throw Error(res._bodyText);
      } else {
        return res.json()
      }
    })
    .catch(err => {
      throw err
    })
}



