import React from 'react';
import styled from 'styled-components';
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';
import Icon from '../../Icon';

import AnyScheduleSectionImg from '../../../img/AnyScheduleSectionImg.svg'

const AnyScheduleSection = props => {
  return (
    <Wrap>
      <img src={AnyScheduleSectionImg} alt="Расписание" />
      <SectionContent>
          <Heading size="2.9" mobile={'34'} lheight={'34'}>
            Любое расписание
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={'24'} >
            От нескольких часов в&nbsp;неделю до&nbsp;постоянного проживания
          </Paragraph>
          <div className="list">
            <SectionListItem>
              <Icon icon="check" />
              <Paragraph size="1.25" mobile={'22'} lheight={'36'} >
                В течение дня
              </Paragraph>
            </SectionListItem>
            <SectionListItem>
              <Icon icon="check" />
              <Paragraph size="1.25" mobile={'22'} lheight={'36'} >
                С ночёвкой
              </Paragraph>
            </SectionListItem>
            <SectionListItem>
              <Icon icon="check" />
              <Paragraph size="1.25" mobile={'22'} lheight={'36'} >
                С проживанием
              </Paragraph>
            </SectionListItem>
            <SectionListItem>
              <Icon icon="check" />
              <Paragraph size="1.25" mobile={''} lheight={''} >
                Временный уход
              </Paragraph>
            </SectionListItem>
          </div>
      </SectionContent>
    </Wrap>
  )
}

const SectionListItem = styled.div`
  display: flex;
  align-items: center;
  i {
    color: #ff6053;
    font-size: 1.6em;
    margin-right: 17px;
  }
  p {
    margin: 0 !important;
    font-weight: 500;
  }
`

const SectionContent = styled.div`
  margin-left: 56px;
 p {
   max-width: 250px;
   margin-top: 10px;
 }
 .list {
   display: flex;
   flex-direction: column;
   margin-top: 16px;
 }
 @media(max-width: 600px) {
   margin-top: 30px;
   margin-left: 0;
   p {
     margin: 0 auto;
     margin-top: 15px;
   }
   .list {
     margin: 0 auto;
     margin-top: 16px;
     width: 250px;
     padding-left: 5%;
     /* max-width: 300px; */
   }
 }
`

const Wrap = styled.div`
  padding-top: 63px;
  padding-bottom: 75px;
  max-width: 800px;
  width: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    max-height: 300px;
  }
  @media(max-width: 600px) {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  @media(max-width: 480px) {
    width: 95%;
    padding-bottom: 50px;
    img {
      width: 90%;
    }
  }
`

export default AnyScheduleSection


