import React from 'react';
import styled from 'styled-components';

//Components
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';

//Images
import guaranteeImg from '../../../img/SecurityPage/guarantee.svg';


const QualityAssuranceSection = props => {
  return (
    <Wrap>
      <img src={guaranteeImg} alt="guarantee" />
      <ContentWrap>
        <Heading size="2.8" mobile={'34'} lheight={'34'}>
          Гарантия качества
        </Heading>
        <Paragraph size="1" mobile={'18'} lheight={'24'}>
          Мы уверены, что всё сделаем хорошо, а если ошибемся <br /> — исправим бесплатно или вернем вам деньги.
        </Paragraph>
      </ContentWrap>
    </Wrap>
  )
}

const ContentWrap = styled.div`
  display: flex;
  flex-direction: column;
  margin-left: 66px;
  p {
    margin-top: 20px;
  }
  @media(max-width: 768px) {
    margin-left: 0;
    margin-top: 30px;
  }
  @media(max-width: 376px) {
    text-align: center;
    p {
      br {
        display: none;
      }
    }
  }
`

const Wrap = styled.div`
  padding-top: 54px;
  padding-bottom: 66px;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  img {
    max-height: 270px;
  }
  @media(max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }
  @media(max-width: 480px) {
    width: 95%;
  }
  @media(max-width: 376px) {
    padding-top: 30px;
    padding-bottom: 25px;
  }
`

export default QualityAssuranceSection


