import React, { Component } from 'react';
import styled from 'styled-components';
import Heading from '../../Heading';
import Icon from '../../Icon';

//Slick slider
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

const settings = {
  dots: true,
  infinite: true,
  slidesToShow: 3,
  slidesToScroll: 1,
  arrows: false,
  autoplay: true,
  autoplaySpeed: 3000,
  centerMode: false,
  responsive: [
    {
      breakpoint: 768,
      settings: {
        slidesToShow: 2,
        centerMode: false
      }
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        centerMode: false
      }
    }
  ]
};

const SliderItem = props => {
  return (
    <SliderItemWrap imgSrc="https://st2.depositphotos.com/1594308/11858/i/950/depositphotos_118584586-stock-photo-romantic-seniors-in-embrace.jpg">
      <PlayButton>
        <Icon icon="play" />
      </PlayButton>
    </SliderItemWrap>
  )
}

class VideoSliderSection extends Component {
  render() {
    return (
      <Wrap>
        <Heading size="2.8" mobile={'34'} lheight={'34'}>
          Специалисты рады вам помочь
        </Heading>
        <SliderWrap>
          <Slider {...settings}>
            <SliderItem>

            </SliderItem>
            <SliderItem>

            </SliderItem>
            <SliderItem>

            </SliderItem>
            <SliderItem>

            </SliderItem>
            <SliderItem>

            </SliderItem>
            <SliderItem>

            </SliderItem>
          </Slider>
        </SliderWrap>
      </Wrap>
    )
  }
}

const PlayButton = styled.div`
  min-width: 44px;
  max-width: 44px;
  min-height: 44px;
  max-height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  
  i {
    color: #222;
    font-size: 28px;
    line-height: 45px;
  }

`

const SliderItemWrap = styled.div`
  box-shadow: 0 3px 3px rgba(69, 83, 99, 0.2);
  border-radius: 8px;
  max-width: 300px;
  overflow: hidden;
  margin: 0 5px;
  background-image: url(${props => props.imgSrc});
  min-height: 190px;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;

  
  @media(max-width: 768px) {
    max-width: 80%;
    margin: auto;
  }
`

const SliderWrap = styled.div`
  max-width: 920px;
  margin: auto;
  margin-top: 46px;

  .slick-dots {
    margin-top: 30px;
    position: relative;
    .slick-active {
      &:before {
        font-size: 10px;
        color: #222;
        opacity: 1;
      }
    }
    button {
      &:before {
        font-size: 10px;
        color: #fff;
        opacity: 1;
      }
    }
    @media(max-width: 768px) {
      margin-top: 10px;
    }
  }
`

const Wrap = styled.div`
  padding-top: 36px;
  padding-bottom: 70px;
  background-color: #edebe3;
  h1 {
    max-width: 380px;
    text-align: center;
    line-height: 53px;
    margin: 0 auto;
    @media(max-width: 768px) {
      line-height: 34px;
    }
    @media(max-width: 376px) {
      padding-left: 10%;
      padding-right: 10%;
    }
  }
 
`

export default VideoSliderSection;


