import React, { Component } from 'react';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import styled from 'styled-components';
import SpecialistSelectionSection from '../../common/Sections/SpecialistSelectionSection';
import Section from '../../common/Sections/FaqSection/index';
import NavLink from '../../common/NavLink/index';
import arrow from '../../img/link-arrow.svg';
import NoMatch from './NoMatch';

class FaqSectionPage extends Component {
  componentDidMount() {
    if(!this.props.faq_list.length) this.props.getFaqs();
    document.title = 'Служба помощи';
    window.scrollTo(0, 0);
  }

  render() {
    
    if (this.props.isLoading) {
      return null
    }
    else if (!this.props.isLoading && (!this.props.data)) {
      return <NoMatch />
    }
    else {
      const {
        slug,
        name
      } = this.props.data;

      return (
        <div>
          <Header />

          <Background>
            <Content>
              <Navigation>
                <NavLink path={'/faq'} name={'Служба помощи'} />
                <Arrow> <img src={arrow} alt='' /> </Arrow>
                <NavLink path={`/faq/${slug}`} name={name} color={'#9b9b9b'} />
              </Navigation>
              <Section data={this.props.data} />
            </Content>
          </Background>

          <SpecialistSelectionSection page='Faq_Section_Page' />
          <Footer />
        </div>
      )
    }
  }
}

// @media(max-width: 480px) {}

const Arrow = styled.div`
    height: 100%;
    margin: 0 2.5%;
    img {
        width: 8px;
    }
`
const Navigation = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding-top: 10px;
    margin-bottom: 20px;
    @media(max-width: 1000px) {
        margin-bottom: -10px;
    }
`
const Background = styled.div`
    background-color: #edebe3;
    width: 100%;
    @media(max-width: 1000px) {
        padding-top: 30px;
    }
`
const Content = styled.div`
    padding-left: 4%;
    padding-right: 4%;
    max-width: 1200px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0 auto;
`

export default FaqSectionPage;





