import React from 'react';
import { connect } from 'react-redux';
import FaqSectionPage from '../components/pages/FaqSectionPage';
import { getFaqs } from '../store/actions/faq.js';

const FaqSectionPageContainer = props => {

  const sectionData = props.faq_list.length && props.faq_list.filter(i => {
    if(i.slug === props.match.params.section) return i
  })[0]

  return (
    <FaqSectionPage {...props} data={sectionData} />
  )
}

const mapStateToProps = state => {
  return {
    faq_list: state.faqStore.faq_list,
    isLoading: state.faqStore.isLoading,
    errors: state.faqStore.errors
  }
}


const mapDispatchToProps = dispatch => {
  return {
    getFaqs: (value) => dispatch(getFaqs(value))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(FaqSectionPageContainer)



