import React, { Component } from 'react';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import styled from 'styled-components';
import SpecialistSelectionSection from '../../common/Sections/SpecialistSelectionSection';
import NavLink from '../../common/NavLink/index';
import arrow from '../../img/link-arrow.svg';
import Column from '../../common/Column/index';
import NoMatch from './NoMatch';

class FaqArticlePage extends Component {
  componentDidMount() {
    if (!this.props.faq_list.length) this.props.getFaqs();
    document.title = 'Служба помощи';
    window.scrollTo(0, 0);
  }
  render() {
    if (this.props.isLoading) {
      return null
    }
    else if (!this.props.isLoading && (!this.props.sectionData || !this.props.subSectionData || !this.props.contentData)) {
      return <NoMatch />
    }
    else {
      const {
        sectionData,
        subSectionData,
        contentData
      } = this.props;
      return (
        <div>
          <Header />

          <Background>
            <Content>
              <Navigation>
                <NavLink path={'/faq'} name={'Служба помощи'} />
                <Arrow> <img src={arrow} alt='' /> </Arrow>
                <NavLink path={`/faq/${sectionData.slug}`} name={sectionData.name} />
                <Arrow> <img src={arrow} alt='' /> </Arrow>
                <NavLink path={`/faq/${sectionData.slug}/${subSectionData.slug}`} name={subSectionData.name} hide={'true'} />
              </Navigation>
              <Title size='2.3' weight='500' > {contentData.post_title} </Title>
              {contentData.post_content}
              {/* <Paragraph>
                      Мы уверены в своих исполнителях и дорожим своей репутацией.
                      Поэтому большинство ситуаций удаётся быстро разрешить своими силами.
                      Параллельно наша служба безопасности работает с клинером, чтобы установить истину.
                      Так что в случае любых оплошностей сразу обращайтесь в службу поддержки — мы готовы помочь 24/7.
              </Paragraph>
                  <Title size='1.3' weight='700' > Check System Requirements </Title>
                  <Paragraph>
                      Some versions of Windows require a certain processor and more hard drive space
                      and memory (RAM) than others. Check the documentation that came with your copy
                      of Windows to find out what you need. Then use System Information to see what
                      your Mac currently has.
              </Paragraph>
                  <Title size='1.3' weight='700' > Get a Windows disk image </Title>
                  <Paragraph>
                      When you buy Windows, it comes as a disk image file (ISO) that you download, or as an
                      installation disc or USB flash drive. If your copy of Windows came on a DVD, you might
                      need to create a disk image of it to work with Boot Camp. If your version of Windows
                      came on a USB flash drive, you can download an ISO from Microsoft.
              </Paragraph>
                  <Title size='1.3' weight='700' > Open Boot Camp Assistant </Title>
                  <Paragraph>
                      Boot Camp Assistant guides you through installing Windows on your Mac. Open this app from
                      the Utilities folder inside the Applications folder. Then follow the onscreen instructions
                      to repartition your startup disk and download related software drivers for Windows.
                      If you're prompted during installation, connect a blank USB 2 flash drive.
              </Paragraph>
                  <Title size='1.3' weight='700' > Format your Windows partition </Title>
                  <Paragraph>
                      When you complete the assistant, your Mac restarts to the Windows installer. When you're asked
                      where you want to install Windows, select the BOOTCAMP partition, then click Format. This step is
                      required only if you're using a flash drive or optical drive to install Windows. In all other cases,
                      the correct partition is selected and formatted for you automatically.
              </Paragraph>
                  <Title size='1.3' weight='700' > Install Windows and Windows Support Software </Title>
                  <Paragraph>
                      Unplug any external devices, such as displays and disks, that aren't necessary during installation.
                      Then follow the onscreen prompts to install Windows. When installation is complete, your Mac starts
                      up in Windows and launches an installer for Windows Support Software. If the screen that says
                      "Welcome to the Boot Camp installer" doesn't appear automatically, you can install Windows Support
                      Software manually.
              </Paragraph>*/}
              <Column
                data={subSectionData}
                url={`${sectionData.slug}`}
                width={'100%'}
                all={true}
                another={true}
              />
            </Content>

          </Background>

          <SpecialistSelectionSection page='Faq_Article'/>
          <Footer />
        </div>
      )
    }
  }
}

const Arrow = styled.div`
    height: 100%;
    margin: 0 2.5%;
    img {
        width: 8px;
    }
`
const Paragraph = styled.p`
    font-size: 1em;
    margin-bottom: 40px;
    line-height: 1.6em;
`
const Navigation = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    max-height: 60px;
    height: 100%;
    width: 100%;
    border-bottom: 2px solid rgba(34,34,34,0.2);
    margin-bottom: 30px;
    @media(max-width: 1000px) {
        max-height: 40px;
    }
`

const Background = styled.div`
    box-sizing: border-box;
    background-color: #edebe3;
    width: 100%;
    @media(max-width: 1000px) {
        padding-top: 50px;
    }
`
const Title = styled.h1`
    font-family: "Alegreya Sans";
    font-size: ${ props => props.size}em;
    font-weight: ${ props => props.weight};
    line-height: 1em;
    margin: 0 0;
    margin-bottom: ${ props => props.bottom || 0}px;
    height: 100%;
    display: flex;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
    margin-bottom: 20px;
`
const Content = styled.div`
    margin-left: 4%;
    margin-right: 4%;
    max-width: 580px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding-bottom: 30px;
    @media(max-width: 768px) {
        padding-left: 10px;
        padding-right: 10px;
    }
`

export default FaqArticlePage;





