import React from 'react';
import styled from 'styled-components';
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';

const IndividualProgramSection = props => {
  return (
    <Wrap>
      <ComponentHeading>
        <Heading size="2.8" mobile={'34'} lheight={'34'}>
          Индивидуальная программа
        </Heading>
        <Paragraph size="1" mobile={'18'} lheight={'24'} >
          Составляем программу ухода или реабилитации и подбираем специалиста, который может ее обеспечить
        </Paragraph>
      </ComponentHeading>
      <ItemsWrap>
        <ComponentItem>
          <Heading size="1.8" mobile={'28'} lheight={'30'} >
            Паллиативный уход
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={'24'} >
            Помощь неизлечимым больным в&nbsp;последней стадии заболевания. Облегчение боли, физическая и психологическая поддержка
          </Paragraph>
        </ComponentItem>

        <ComponentItem>
          <Heading size="1.8" mobile={'20'} lheight={'30'} >
            Нарушение мобильности
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={'24'} >
            Физически сильный специалист с медицинской подготовкой и подходящим опытом помогает подопечному безопасно передвигаться и следит за&nbsp;состоянием кожи
          </Paragraph>
        </ComponentItem>
        <ComponentItem>
          <Heading size="1.8" mobile={'28'} lheight={'30'} >
            Деменция
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={'24'} >
            Правильный уход за&nbsp;подопечными с деменцией уменьшает симптомы заболевания: депрессию, отказ от социальной жизни, нервозность. Специалист помогает подопечному упражнять память и выполнять ежедневную рутину, замедлять когнитивные и поведенческие изменения
          </Paragraph>
        </ComponentItem>
        <ComponentItem>
          <Heading size="1.8" mobile={'28'} lheight={'30'} >
            Неврологические расстройства
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={'24'} >
            Создаем индивидуальную программу ухода, которая учитывает симптомы и скорость развития заболевания. Работаем с подопечными любого возраста.
          </Paragraph>
        </ComponentItem>
        <ComponentItem>
          <Heading size="1.8" mobile={'28'} lheight={'30'} >
            Альцгеймер
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={'24'} >
            Такой диагноз сильно влияет на привычную жизнь человека и его эмоциональное состояние. Лекарства нет, но можно обеспечить уход, который поможет человеку жить комфортно и безопасно, социализироваться, упражнять память, выполнять ежедневную рутину и сохранять независимость, насколько это возможно
          </Paragraph>
        </ComponentItem>
      </ItemsWrap>
    </Wrap>
  )
}
const ComponentItem = styled.div`
  box-shadow: 0 3px 3px rgba(69, 83, 99, 0.2);
  border-radius: 8px;
  background-color: #ffffff;
  padding: 16px 20px;
  width: 30%;
  display: inline-block;
  box-sizing: border-box;
  margin: 5px 5px;
  width: 100%;
  max-width: 350px;
  text-align: left !important;
  direction: ltr;
  h1 {
    margin-bottom: 16px;
    line-height: 1em;
  }
  p {
    max-width: 260px;
  }
  
`

const ItemsWrap = styled.div`
  column-count: 3;
  column-gap: 5px;
  column-width: 30%;
  padding-top: 32px;
  direction: rtl;
  max-width: 1024px;
  margin: 0 auto;
  
  /* align-items: center; */
  @media(max-width: 768px) {
    column-count: 2;
  }
  @media(max-width: 600px) {
    column-count: none;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`

const ComponentHeading = styled.div`
  text-align: center;
  h1 {
    margin-bottom: 20px;
  }
  p {
    max-width: 500px;
    margin: auto;
  }
`

const Wrap = styled.div`
  background-color: #edebe3;
  padding-top: 66px;
  padding-bottom: 98px;
  padding-left: 5%;
  padding-right: 5%;
  @media(min-width: 1440px) {
    padding-left: 20%;
    padding-right: 20%;
  }
`

export default IndividualProgramSection


