import React from 'react';
import styled from 'styled-components';

//Components
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';

//Images
import pnoneIcon from '../../../img/SecurityPage/phone.svg';
import documentsIcon from '../../../img/SecurityPage/documents.svg';
import mailIcon from '../../../img/SecurityPage/mail.svg';
import recordPlayerIcon from '../../../img/SecurityPage/record_player.svg';

const VerificationOfSpecialists = props => {
  return (
    <Wrap>
      <SectionHeading>
        <Heading size="2.8" mobile={'34'} lheight={'34'}>
          Проверка специалистов
        </Heading>
        <Paragraph size="1" mobile={'18'} lheight={'24'}>
          Мы изучаем прошлое всех кандидатов <br /> и знакомимся с&nbsp;ними лично
        </Paragraph>
      </SectionHeading>
      <CardsList>
        <CardItem>
          <CardItemImgWrap>
            <img src={pnoneIcon} alt="pnoneIcon" />
          </CardItemImgWrap>
          <CardItemContent>
            <Heading size="1.8" mobile={'24'} lheight={'30'}>
              Интервью по телефону
            </Heading>
            <Paragraph mobile={'18'} lheight={'24'}>
              Предварительное знакомство, вопросы об&nbsp;опыте, квалификации, рабочем графике
            </Paragraph>
          </CardItemContent>
        </CardItem>
        <CardItem>
          <CardItemImgWrap>
            <img src={recordPlayerIcon} alt="recordPlayerIcon" />
          </CardItemImgWrap>
          <CardItemContent>
            <Heading size="1.8" mobile={'24'} lheight={'30'}>
              Личное интервью
            </Heading>
            <Paragraph mobile={'18'} lheight={'24'}>
              Разговор лицом к&nbsp;лицу и дополнительное тестирование
            </Paragraph>
          </CardItemContent>
        </CardItem>
        <CardItem>
          <CardItemImgWrap>
            <img src={documentsIcon} alt="documentsIcon" />
          </CardItemImgWrap>
          <CardItemContent>
            <Heading size="1.8" mobile={'24'} lheight={'30'}>
              Проверка документов
            </Heading>
            <Paragraph mobile={'18'} lheight={'24'}>
              Мы не сотрудничаем с&nbsp;теми, кто вызывает хоть малейшее подозрение в&nbsp;нечестности
            </Paragraph>
          </CardItemContent>
        </CardItem>
        <CardItem>
          <CardItemImgWrap>
            <img src={mailIcon} alt="mailIcon" />
          </CardItemImgWrap>
          <CardItemContent>
            <Heading size="1.8" mobile={'24'} lheight={'30'}>
              Рекомендации
            </Heading>
            <Paragraph mobile={'18'} lheight={'24'}>
              Всегда собираем отзывы семей, с&nbsp;которыми кандидат работал в&nbsp;прошлом
            </Paragraph>
          </CardItemContent>
        </CardItem>
      </CardsList>
    </Wrap>
  )
}

const CardItemImgWrap = styled.div`
  min-width: 120px;
  max-width: 120px;
  display: flex;
  justify-content: center;
  img {
    max-width: 120px;
    max-height: 110px;
  }
  @media(max-width: 768px) {
    justify-content: flex-start;
  }
`

const CardItemContent = styled.div`
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  margin-left: 34px;
  p {
    margin-top: 5px;
    max-width: 350px;
  }
  @media(max-width: 768px) {
    margin-left: 0;
    margin-top: 30px;
  }
`

const CardItem = styled.div`
  display: flex;
  align-items: center;
  padding: 35px 40px;
  box-shadow: 0 3px 3px rgba(69, 83, 99, 0.2);
  border-radius: 8px;
  background-color: #ffffff;
  margin-bottom: 10px;
  width: 100%;
  max-width: 620px;
  box-sizing: border-box;
  @media(max-width: 768px) {
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
  }
  
`

const CardsList = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  margin-top: 20px;
  /* max-height: 160px */
  @media(max-width: 500px) {
    width: 100%;
  }
`

const SectionHeading = styled.div`
  text-align: center;
  h1 {
    margin-bottom: 20px;
  }
`

const Wrap = styled.div`
  padding: 0 10%;
  padding-top: 36px;
  padding-bottom: 60px;
  background-color: #edebe3;

  @media(max-width: 500px) {
    padding: 0 5%;
    padding-top: 36px;
    padding-bottom: 60px;
  }
  
`

export default VerificationOfSpecialists


