import React from 'react';
import styled from 'styled-components';
import ItemLink from './ItemLink/index';
import { Link } from 'react-router-dom';

const MAX_POSTS_LENGTH = 5

const Column = props => {
  const {
    slug,
    name,
    posts
  } = props.data;

  let articles = props.all
    ? posts
    : posts.slice(0, MAX_POSTS_LENGTH);

  let title = props.another
    ? "Другие статьи из раздела"
    : name;

  let color = props.another ? '#999' : '#222';
  console.log('props section', props);
  return (
    <Wrap width={props.width || ''}>
      <Title size='1.35' weight='700' color={color} bold={props.boldTitle + ''}>
        <StyledLink to={`/faq/${props.url}/${slug}`}>
          {name}
        </StyledLink>
      </Title>
      <ColumnItems bottom={props.all} >
        <ul>
          {
            articles.map((item, i) => {
              return (
                <li key={i}>
                  <ItemLink
                    data={item}
                    path={`/faq/${props.url}/${slug}/${item.post_name}`}
                  />
                </li>
              )
            })
          }
        </ul>
        {
          posts.length < MAX_POSTS_LENGTH ?
            null
            :
            <StyledLink to={`/faq/section/sub_section`} left='20' big={true}>
              Еще
              <LinkNumber>
                {
                  posts.length - MAX_POSTS_LENGTH
                }
              </LinkNumber>
              статьи
            </StyledLink>
        }

      </ColumnItems>
    </Wrap>
  )
}

const Wrap = styled.div`
    max-width: ${ props => props.width || '30%'};
    flex: 1 1 280px;
    min-width: 280px;
    @media(max-width: 960px) {
        max-width: ${ props => props.width || '70%'}; 
        width: ${ props => props.width || '70%'}; 
    }
`
const LinkNumber = styled.span`
    padding: 0 5px;
    font-size: 0.8em;
`
const StyledLink = styled(Link)`
    margin-top: 15px;
    color: #ff6053;
    display: block;
    width: 100%;
    font-size: 1em;
    text-decoration: none;
    font-weight: 700;
    min-height: 18px;
    padding-bottom: 8px;
    padding-left: ${ props => props.left || 0}px;
    font-family: "Alegreya Sans";
    transition: ease 0.3s all;
    &:hover {
        font-weight: 700;
        color: #ff6053;
    } 
    @media(max-width: 960px) {
      font-size: ${ props => props.big ? '1.4' : '1'}em;
    }
    /* @media(max-width: 376px) {
      font-size: 18px;
      line-height: 20px;
    } */
`
const ColumnItems = styled.div`
    border-bottom: ${ props => props.bottom ? 0 : 2}px solid rgba(34,34,34,0.7);
    margin-bottom: 30px;
    ul {
        margin-top: 5px;
        padding-left: 20px;
    }
    ul li {
        padding-top: 10px;
    }
`
const Title = styled.h1`
    font-size: ${ props => props.bold === 'true' ? +props.size + 1.5 : props.size}em;
    font-weight: ${ props => props.weight};
    margin: 0 0;
    border-bottom: 2px solid rgba(34,34,34,0.65);
    display: flex;
    align-items: center;
    box-sizing: border-box;
    a {
        font-weight: 700;
        color: ${ props => props.color};
    }
    @media(max-width: 768px) {
        margin-top: 0
    }
    @media(max-width: 376px) {
      font-size: 24px;
      line-height: 20px;
    }
`

export default Column


