import React, { Component } from 'react';
import styled from 'styled-components';
import TagManager from 'react-gtm-module';

//Images
import ReliablePerformersSection from '../../img/ReliablePerformersSection.svg';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import SpecialistSelectionSection from '../../common/Sections/SpecialistSelectionSection';
import QuestionsAndAnswersSection from '../../common/Sections/QuestionsAndAnswersSection';
import SpecialistsSections from '../../common/Sections/SpecialistsSections';
import YouCanBeCalmSection from '../../common/Sections/YouCanBeCalmSection';
import VideoSliderSection from '../../common/Sections/VideoSliderSection';
import Paragraph from '../../common/Paragraph';
import Heading from '../../common/Heading';
import SendNumber from '../../common/SendNumber/index';

const tagManagerArgs = {
  dataLayer: {
      userId: '001',
      userProject: 'project',
      page: 'specialists'
  },
  dataLayerName: 'PageDataLayer'
}

class SpecialistsPage extends Component {

  componentDidMount() {
    TagManager.dataLayer(tagManagerArgs);
    document.title = 'Специалисты';
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div>
        <Header />
        <OurSpecialists>
          <img src={ReliablePerformersSection} alt="" />
          <Heading size="2.9" mobile={'34'} lheight={'34'}>
            Наши специалисты
          </Heading>
          <Paragraph size="1.35" mobile={'18'} lheight={'22'}>
            Кто-то из них может помочь вам заботиться о&nbsp;вашем родственнике
          </Paragraph>
          <OurSpecialistsButtonsWrap>
            <SendNumber page='specialist_page'/>
          </OurSpecialistsButtonsWrap>
        </OurSpecialists>
        <SpecialistsSections />
        <YouCanBeCalmSection />
        <VideoSliderSection />
        <SpecialistSelectionSection page='specialist_page'/>
        <QuestionsAndAnswersSection />
        <Footer />
      </div>
    )
  }
}

const OurSpecialistsButtonsWrap = styled.div`
  display: flex;
  align-items: center;
  /* margin-top: 20px; */
  button {
    margin-left: 10px;
  }

  @media(max-width: 530px) {
    flex-direction: column;
    button {
      margin: 0 auto;
      margin-top: 12px;
      width: 100%;
    }
    input: {
      width: 100%;
    }
  }
`

const OurSpecialists = styled.div`
  background-color: #edebe3;
  padding: 50px 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  border-bottom: 2px solid #d8d7cf;
  img {
    max-height: 280px;
  }
  p {
    max-width: 480px;
    margin-top: 20px;
  }
  h1 {
    margin: 4px 0;
  }

  @media(max-width: 768px) {
    margin-top: 20px;
  }

  @media(max-width: 480px) {
    img {
      width: 100%;
    }
  }
`


export default SpecialistsPage


