import React from 'react';
import styled from 'styled-components';
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';
// import Icon from '../../Icon';

//Column icons 
import appleIcon from '../../../img/TypicalTasksSectionIcons/apple.svg';
import dropIcon from '../../../img/TypicalTasksSectionIcons/drop.svg';
import dumbbellIcon from '../../../img/TypicalTasksSectionIcons/dumbbell.svg';
import hearthIcon from '../../../img/TypicalTasksSectionIcons/hearth.svg';
import helicopterIcon from '../../../img/TypicalTasksSectionIcons/helicopter.svg';
import leefsIcon from '../../../img/TypicalTasksSectionIcons/leefs.svg';
import medicationsIcon from '../../../img/TypicalTasksSectionIcons/medications.svg';
import phoneIcon from '../../../img/TypicalTasksSectionIcons/phone.svg';

const TypicalTasksSection = props => {
  return (
    <Wrap>
      <Heading size="2.8" mobile={'34'} lheight={'34'}>
        Типичные задачи наших специалистов
      </Heading>
      <SectionListWrap>
        <div className="column">
          <SectionListItem>
            <ImgWrapping>
              <img src={medicationsIcon} alt="" />
            </ImgWrapping>
            <div className="contentWrap">
              <Heading size="1.8" mobile={'24'} lheight={'24'}>
                Лекарства и процедуры
              </Heading>
              <Paragraph size="1" mobile={'16'} lheight={'20'}>
                Напоминания и помощь
              </Paragraph>
            </div>
          </SectionListItem>
          <SectionListItem>
            <ImgWrapping>
              <img src={dropIcon} alt="" />
            </ImgWrapping>
            <div className="contentWrap">
              <Heading size="1.8" mobile={'24'} lheight={'24'}>
                Личная гигиена
              </Heading>
              <Paragraph size="1" mobile={'16'} lheight={'20'}>
                Купание и  уход
              </Paragraph>
            </div>
          </SectionListItem>
          <SectionListItem>
            <ImgWrapping>
              <img src={appleIcon} alt="" />
            </ImgWrapping>
            <div className="contentWrap">
              <Heading size="1.8" mobile={'24'} lheight={'24'}>
                Еда
              </Heading>
              <Paragraph size="1" mobile={'16'} lheight={'20'}>
                Продукты и готовка
              </Paragraph>
            </div>
          </SectionListItem>
          <SectionListItem>
            <ImgWrapping>
              <img src={leefsIcon} alt="" />
            </ImgWrapping>
            <div className="contentWrap">
              <Heading size="1.8" mobile={'24'} lheight={'24'}>
                Легкая уборка
              </Heading>
              <Paragraph size="1" mobile={'16'} lheight={'20'}>
                Посуда и пыль, стирка
              </Paragraph>
            </div>
          </SectionListItem>
        </div>
        <div className="column">
          <SectionListItem>
          <ImgWrapping>
            <img src={dumbbellIcon} alt="" />
          </ImgWrapping>
            <div className="contentWrap">
              <Heading size="1.8" mobile={'24'} lheight={'24'}>
                Активная жизнь
              </Heading>
              <Paragraph size="1" mobile={'16'} lheight={'20'}>
                Прогулки и легкая гимнастика
              </Paragraph>
            </div>
          </SectionListItem>
          <SectionListItem>
          <ImgWrapping>
            <img src={phoneIcon} alt="" />
          </ImgWrapping>
            <div className="contentWrap">
              <Heading size="1.8" mobile={'24'} lheight={'24'}>
                Поддержка 24/7
              </Heading>
              <Paragraph size="1" mobile={'16'} lheight={'20'}>
                Ассистент по&nbsp;телефону
              </Paragraph>
            </div>
          </SectionListItem>
          <SectionListItem>
          <ImgWrapping>
            <img src={helicopterIcon} alt="" />
            </ImgWrapping>
            <div className="contentWrap">
              <Heading size="1.8" mobile={'24'} lheight={'24'}>
                Транспорт
              </Heading>
              <Paragraph size="1" mobile={'16'} lheight={'20'}>
                Сопровождение по&nbsp;городу
              </Paragraph>
            </div>
          </SectionListItem>
          <SectionListItem>
          <ImgWrapping>
            <img src={hearthIcon} alt="" />
            </ImgWrapping>
            <div className="contentWrap">
              <Heading size="1.8" mobile={'24'} lheight={'24'}>
                Приятная компания
              </Heading>
              <Paragraph size="1" mobile={'16'} lheight={'20'}>
                Поговорить или помолчать
              </Paragraph>
            </div>
          </SectionListItem>
        </div>
      </SectionListWrap>
    </Wrap>
  )
}

const ImgWrapping = styled.div`
  min-width: 60px;
  max-width: 70px;
  text-align: center;
  @media(max-width: 376px) {
    transform: scale(0.8)
  }
`

const SectionListItem = styled.div`
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  text-align: left !important;
  .contentWrap {
    display: flex;
    flex-direction: column;
    margin-left: 22px;  
    h1 {
      line-height: 1em;
      margin: 0;
    }
  }
`

const SectionListWrap = styled.div`
  display: flex;
  justify-content: space-around;
  margin-top: 60px;
  
  .column {
    display: flex;
    flex-direction: column;
  
  }
  .column:last-child {
    text-align: right !important;
  }

  @media(max-width: 800px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .column:nth-of-type(1) {
      padding-left: 5.5%;
    }
    .column:nth-of-type(2) {
      margin-left: -1.5%;
    }
  }
  @media(max-width: 360px) {
    .column:nth-of-type(1) {
      padding-left: 0;
    }
    .column:nth-of-type(2) {
      margin-left: 0;
    }
  }
`

const Wrap = styled.div`
  padding-top: 47px;
  padding-bottom: 75px;
  max-width: 770px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  h1 {
    max-width: 470px;
    margin: 0 auto;
    line-height: 52px;
  }

  @media(max-width: 768px) {
    h1 {
      line-height: 38px;
    }
  }
  @media(max-width: 480px) {
    width: 95%;
    h1 {
      margin-bottom: -25px;
    }
  }
`

export default TypicalTasksSection


