import React from 'react';
import styled from 'styled-components';

//Components
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';

//Images
import doc1Img from '../../../img/documentsImg/docs1.png'

const ImportantDocumentsSection = props => {
  return (
    <Wrap>
      <Heading size="2.3" mobile={'34'} lheight={'46'}>
        Важные документы
      </Heading>
      <DocumentsWrap>
        <DocumentItem>
          <img src={doc1Img} alt="" />
          <DocumentsInfo>
            <Paragraph size="0.9" mobile={'16'} lheight={'20'}>
              Устав Академии
          </Paragraph>
          <a href="/documents">
            Скачать PDF  2 MB
          </a>
          </DocumentsInfo>
        </DocumentItem>
        <DocumentItem>
          <img src={doc1Img} alt="" />
          <DocumentsInfo>
            <Paragraph size="0.9" mobile={'16'} lheight={'20'}>
              Устав Академии
          </Paragraph>
          <a href="/documents">
            Скачать PDF  2 MB
          </a>
          </DocumentsInfo>
        </DocumentItem>
        <DocumentItem>
          <img src={doc1Img} alt="" />
          <DocumentsInfo>
            <Paragraph size="0.9" mobile={'16'} lheight={'20'}>
              Устав Академии
          </Paragraph>
          <a href="/documents">
            Скачать PDF  2 MB
          </a>
          </DocumentsInfo>
        </DocumentItem>
        <DocumentItem>
          <img src={doc1Img} alt="" />
          <DocumentsInfo>
            <Paragraph size="0.9" mobile={'16'} lheight={'20'}>
              Устав Академии
          </Paragraph>
          <a href="/documents">
            Скачать PDF  2 MB
          </a>
          </DocumentsInfo>
        </DocumentItem>
      </DocumentsWrap>
    </Wrap>
  )
}

const DocumentsInfo = styled.div`
  position: relative;
  left: -10px;
  p {
    max-width: 191px;
  }
  a {
    text-decoration: none;
    color: #ff6053;
    font-weight: 700;
    font-size: 0.9em;
    font-family: "Alegreya Sans";
    margin-top: 10px;
  }
  @media(max-width: 376px) {
    a {
      font-size: 16px;
      line-height: 20px;
    }
  }
`

const DocumentItem = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 160px;
  min-width: 160px;
  img {
    width: 100%;
    max-width: 160px;
  }

  @media(max-width: 768px) {
    width: 45%;
    margin-bottom: 20px;
  }
  
`

const DocumentsWrap = styled.div`
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
  overflow: auto;
  width: 100%;
`

const Wrap = styled.div`
  padding: 0 10%;
  padding-top: 26px;
  padding-bottom: 40px;
  background-color: #edebe3;
  border-bottom: 2px solid #d8d7cf;
  text-align: center;
  @media(max-width: 1000px) {
    padding: 0 7%;
    padding-top: 60px;
  }
  @media(max-width: 480px) {
    padding-left: 10px;
    padding-right: 0;
  }
`

export default ImportantDocumentsSection


