import React, { Component } from 'react';
import styled from 'styled-components';
import TagManager from 'react-gtm-module';

//Images
import HelpsToTakeCareSectionOfImg from '../../img/HelpsToTakeCareSectionOfImg.svg';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import SpecialistSelectionSection from '../../common/Sections/SpecialistSelectionSection';
import QuestionsAndAnswersSection from '../../common/Sections/QuestionsAndAnswersSection';
import AnyScheduleSection from '../../common/Sections/AnyScheduleSection';
import IndividualProgramSection from '../../common/Sections/IndividualProgramSection';
import TypicalTasksSection from '../../common/Sections/TypicalTasksSection';
import CostOfServicesSection from '../../common/Sections/CostOfServicesSection';
import Heading from '../../common/Heading';
import Paragraph from '../../common/Paragraph';
import SendNumber from '../../common/SendNumber/index';

const tagManagerArgs = {
  dataLayer: {
      userId: '001',
      userProject: 'project',
      page: 'uslugi'
  },
  dataLayerName: 'PageDataLayer'
}

class UslugiPage extends Component {

  componentDidMount() {
    TagManager.dataLayer(tagManagerArgs);
    document.title = 'Услуги';
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div>
        <Header />
        <HelpsToTakeCareSectionOf>
          <img src={HelpsToTakeCareSectionOfImg} alt="" />
          <Heading size="2.9" mobile={'34'} lheight={'34'}>
            Персональный подход
          </Heading>
          <Paragraph size="1.35" mobile={'18'} lheight={'22'} >
            Каждый подопечный уникален, у всех разные потребности и желания — это необходимо учитывать, что мы и делаем
          </Paragraph>
          <HelpsToTakeCareSectionButtonsWrap>
            <SendNumber page='uslugi_page'/>
          </HelpsToTakeCareSectionButtonsWrap>
        </HelpsToTakeCareSectionOf>
        <AnyScheduleSection />
        <IndividualProgramSection />
        <TypicalTasksSection />
        <CostOfServicesSection />
        <SpecialistSelectionSection page='uslugi_page'/>
        <QuestionsAndAnswersSection />
        <Footer />
      </div>
    )
  }
}

const HelpsToTakeCareSectionButtonsWrap = styled.div`
  display: flex;
  align-items: center;
  /* margin-top: 20px; */
  
  button {
    margin-left: 10px;
  }

  @media(max-width: 530px) {
    flex-direction: column;
    button {
      margin: 0 auto;
      margin-top: 12px;
      width: 100%;
    }
    input: {
      width: 100%;
    }
  }
`

const HelpsToTakeCareSectionOf = styled.div`
  background-color: #edebe3;
  padding: 50px 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  img {
    max-height: 235px;
  }
  p {
    max-width: 520px;
    margin-top: 20px;
  }
  h1 {
    margin: 4px 0;
  }

  @media(max-width: 768px) {
    margin-top: 20px;
  }

  @media(max-width: 480px) {
    img {
      width: 100%;
    }
  }
`

export default UslugiPage


