import {
  REQUEST_FAQ,
  SUCCES_FAQ,
  ERROR_FAQ
} from '../constants/faq.js';

const query = (url) =>  ({
  method: 'get',
  headers: {  
      'Accept': 'application/json',
      'Content-Type': 'application/json'
  },
  urlParams: url
})

export const getFaqs = ( payload ) => {
  return {
      type: 'API',
      payload: {
          query: query('categories'),
          variables: null,
          actions: [REQUEST_FAQ, SUCCES_FAQ, ERROR_FAQ]
      }
  }
}



