import React, { Component } from 'react';
import styled from 'styled-components';
import { NavLink, Link } from 'react-router-dom';
import Icon from '../Icon';
import Heading from '../Heading';
import Paragraph from '../Paragraph';

import Logo from '../../img/logo.svg';
import LogoTitle from '../../img/LogoTitle.svg';
import LogoTitleWhite from '../../img/LogoTitleWhite.svg';

export default class Header extends Component {
  state = {
    openMobileMenu: false,
    openMobileProfileMenu: false,
  }

  onMobileMenu = () => this.setState({ openMobileMenu: !this.state.openMobileMenu });

  onMobileProfile = (e) => {
    e.preventDefault();

    this.setState({ openMobileProfileMenu: !this.state.openMobileProfileMenu })
  }

  render() {
    return [

      <HeaderContainer key={'header-container'}>
          <BackgroundColor>
            <HeaderTopWrap>
                <HeaderPhoneNumber>
                  +7 (495) 565-36-50
                </HeaderPhoneNumber>
                <HeaderTopMenu>
                  <HeaderTopMenuLink activeClassName="active" to="/">
                    Организация ухода
                  </HeaderTopMenuLink>
                  <HeaderTopMenuLinkA href='http://blizkie.ru/academy' target="_self">
                      Академия Близких
                  </HeaderTopMenuLinkA>
                  <HeaderTopMenuLink activeClassName="active-top" to="/" disabled={true}>
                    Журнал (скоро)
                  </HeaderTopMenuLink>
                  <HeaderTopMenuButtonsWrap>
                    <HeaderTopMenuButton red='true' to="/">
                      Найти помощника
                    </HeaderTopMenuButton>
                  {/* <HeaderTopMenuButton to="/">
                    <Icon icon="account" />
                    Войти
                    </HeaderTopMenuButton> */}
                  </HeaderTopMenuButtonsWrap>
                </HeaderTopMenu>
            </HeaderTopWrap>
          </BackgroundColor>
          <HeaderMenuBottomWrap>
          <HeaderBottomLogoWrap to="/">
            <img alt="Logo" src={Logo} />
            <img alt="LogoTitle" src={LogoTitle} className='logo-title'/>
          </HeaderBottomLogoWrap>
          <HeaderBottonMenuWrap>
            <HeaderMenuBottomLin activeClassName="active" to="/uslugi">
              Услуги
            </HeaderMenuBottomLin>
            <HeaderMenuBottomLin activeClassName="active" to="/specialists">
              Специалисты
            </HeaderMenuBottomLin>
            <HeaderMenuBottomLin activeClassName="active" to="/security">
              Безопасность
            </HeaderMenuBottomLin>
            <HeaderMenuBottomLin activeClassName="active" to="/faq">
              Служба помощи
            </HeaderMenuBottomLin>
          </HeaderBottonMenuWrap>
        </HeaderMenuBottomWrap>
      </HeaderContainer>,
      <MobileHeaderWrap key={'mobile-header-wrapper'}>
        <MobileHeading>
          <Link to="/">
            <img alt="LogoTitle" src={LogoTitleWhite} className='logo-title-white'/>
          </Link>
        </MobileHeading>
        <MobileBurgerBtn onClick={this.onMobileMenu}>
          {!this.state.openMobileMenu ?
            <Icon icon="menu" />
            :
            <Icon icon="close" />
          }

        </MobileBurgerBtn>
      </MobileHeaderWrap>,
      <MobileMenu open={this.state.openMobileMenu} key={'mobile-menu'} >
        <div className="menu_wrap">
          <HeaderMenuBottomLin activeClassName="active" to="/uslugi">
            Услуги
          </HeaderMenuBottomLin>
          <HeaderMenuBottomLin activeClassName="active" to="/specialists">
            Специалисты
            </HeaderMenuBottomLin>
          <HeaderMenuBottomLin activeClassName="active" to="/security">
            Безопасность
            </HeaderMenuBottomLin>
          <HeaderMenuBottomLin activeClassName="active" to="/faq">
            Служба помощи
          </HeaderMenuBottomLin>
          <HeaderMenuBottomLin activeClassName="active-none" onClick={this.onMobileProfile} to="/">
            Профиль
          </HeaderMenuBottomLin>
          <HeaderPhoneNumber>
            +7 (495) 565-36-50
          </HeaderPhoneNumber>
          <HeaderTopMenuButton activeClassName="active" red='true' to="/">
            Найти помощника
          </HeaderTopMenuButton>
        </div>
      </MobileMenu>,
      <MobileProfileMenu open={this.state.openMobileProfileMenu} key={'mobile-profile-menu'}>
        <Heading size="2.5">
          Профиль
        </Heading>
        <div className="menu_wrap">
          <HeaderMenuBottomLin activeClassName="active-none" to="/">
            Оплата
          </HeaderMenuBottomLin>
          <HeaderMenuBottomLin activeClassName="active-none" to="/settings">
            Настройки
          </HeaderMenuBottomLin>
          <HeaderMenuBottomLin activeClassName="active-none" to="">
            Выход
          </HeaderMenuBottomLin>
        </div>
        <div onClick={this.onMobileProfile} className="back_btn">
          <Icon icon="chevron-left" />
          <Paragraph size="2">
            Назад
         </Paragraph>
        </div>
      </MobileProfileMenu>
    ]
  }
}

const MobileProfileMenu = styled.div`
  display: ${props => props.open ? 'block' : 'none'};
  min-height: 100vh;
  background-color: #222;
  border-top: 1px solid #444;
  position: fixed;
  top: 40px;
  width: 100%;
  z-index: 11111;
  h1 {
    color: #ff6053;
    margin-top: 37px;
    text-align: center;

  }
  .menu_wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    a {
      font-size: 2.9em;
    }
    
  }
  .back_btn {
    display: flex;
    align-items: center;
    color: #fff;
    width: auto;
    margin: auto;
    margin-top: 70px;
    text-align: center;
    justify-content: center;
    position: relative;
    left: -10px;
    p {
      color: #fff;
      line-height: 0;
    }
    i {
      color: #fff;
      font-size: 2.5em;
      
    }
  }
`

const MobileMenu = styled.div`
  display: ${props => props.open ? 'block' : 'none'};
  min-height: 100vh;
  background-color: #222;
  border-top: 1px solid #444;
  position: fixed;
  top: 40px;
  width: 100%;
  z-index: 10000;
  .menu_wrap {
    width: 90%;
    margin: 0 auto;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    a {
      font-size: 35px;
      font-weight: 500;
      margin: 5px 0; 
    }
  }

  @media(min-width: 1001px) {
    display: none;
  }
`

const MobileBurgerBtn = styled.div`
   i {
     color: #fff;
     font-size: 2em;
     cursor: pointer;
   }
`

const MobileHeading = styled.div`
  .logo-title-white {
    width: 100px;
  }
`

const MobileHeaderWrap = styled.div`
  display: none;
  background-color: #222222;
  min-height: 40px;
  padding-left: 2.5%;
  padding-right: 2.5%;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  top: 0;
  z-index: 10000;
  margin-bottom: 80px;
  @media(max-width: 1000px) {
    display: flex;
  }
`

const HeaderMenuBottomLin = styled(NavLink)`
  font-size: 1.3em;
  color: #222222;
  font-weight: 500;
  text-decoration: none;
  transition: ease 0.5s all;
  font-family: "Alegreya Sans";
  &:hover, &.active {
    color: #ff6053;
  }
  @media(max-width: 1000px) {
    color: #fff;
  }
`

const HeaderBottonMenuWrap = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 60%;

`

const HeaderBottomLogoWrap = styled(Link)`
  width: 25%;
  display: flex;
  align-items: center;
  text-decoration: none;
  img {
    width: 40px;
    height: 40px;
  }
  .logo-title {
    height: 35px;
    width: 170px;
    margin-left: 20px;
  }
  h1 {
    margin: 0;
    margin-left: 20px;
    color: #ff6053;
    font-size: 2.5em;
    font-weight: 400;
  }
`

const HeaderMenuBottomWrap = styled.div`
  display: flex;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 28px;
  padding-left: 15px;
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  box-sizing: border-box;
`

const HeaderTopMenuButtonsWrap = styled.div`
  min-height: 40px;
  box-sizing: border-box;
  display: flex;
`

const HeaderTopMenuButton = styled(Link)`
  box-sizing: border-box;
  color: #fff;
  font-size: 0.8em;
  background-color: ${props => props.red ? "#ff6053" : 'transparent'};
  text-decoration: none;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  height: 40px;
  transition: ease 0.5s all;
  font-family: "Alegreya Sans";
  font-weight: 500;
  &:hover {
    background-color: ${props => props.red ? "#c74c42" : 'transparent'};
    color: ${props => !props.red ? '#ff6053' : '#fff'}
  }
  i {
    font-size: 1em;
    margin-right: 2px;
  }
  @media(max-width: 1000px) {
    font-size: 1em !important;
    border-radius: 4px;
    box-sizing: content-box;
    height: auto;
    padding: 15px 50px;
  }
`

const HeaderTopMenuLink = styled(NavLink)`
  color: #fff;
  font-size: 0.8em;
  text-decoration: none;
  transition: ease 0.5s all;
  box-sizing: border-box;
  border-bottom: 4px solid transparent;
  font-family: "Alegreya Sans";
  min-height: 40px;
  max-height: 40px;
  padding-top: 10px;
  opacity: ${ props => props.disabled ? 0.6 : 1 };
  &:hover, &.active {
    /* pointer-events: ${ props => props.disabled ? 'none' : 'auto' } ; */
    color: ${ props => props.disabled ? '#fff' : '#ff6053' } ;
    border-bottom: ${ props => props.disabled ? 0 : 4}px solid #ff6053;
  }
`

const HeaderTopMenuLinkA = styled.a`
  font-size: 0.8em;
  text-decoration: none;
  transition: ease 0.5s all;
  box-sizing: border-box;
  border-bottom: 4px solid transparent;
  font-family: "Alegreya Sans";
  min-height: 40px;
  max-height: 40px;
  padding-top: 10px;
  opacity: ${ props => props.disabled ? 0.6 : 1 };
  color: #fff;
  &:hover, &.active {
    /* pointer-events: ${ props => props.disabled ? 'none' : 'auto' } ; */
    color: ${ props => props.disabled ? '#fff' : '#ff6053' } ;
    border-bottom: ${ props => props.disabled ? 0 : 4}px solid #ff6053;
  }
`

// padding-right: 100px; пока нету ссылки войти
const HeaderTopMenu = styled.div`
  width: 54%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 100px; 
  @media(max-width: 1256px) {
    width: 60%;
  }
`

const BackgroundColor = styled.div`
  background-color: #222222;
`

const HeaderTopWrap = styled.div`
  width: 100%;
  max-width: 1024px;
  min-height: 40px;
  max-height: 40px;
  padding-left: 15px;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const HeaderContainer = styled.div`
  width: 100%;
  @media(max-width: 1000px) {
    display: none;
  }
`

const HeaderPhoneNumber = styled.p`
  color: #fff;
  font-size: 1em;
  font-family: "Alegreya Sans";
  font-weight: 500;
  @media(max-width: 1000px) {
    border: 1px solid #444;
    border-radius: 4px;
    font-size: 1em !important;
    box-sizing: content-box;
    height: auto;
    padding: 15px 50px;
    width: auto;
    margin-top: 30px !important;
  }
`


