import React, { Component } from 'react';
import styled from 'styled-components';

//Images 
import SpecialistSelectionSectionImg from '../../../img/SpecialistSelectionSectionImg.svg';

//Components
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';
import ParagraphPX from '../../ParagraphPX';
import SendNumber from '../../SendNumber/index';

class SpecialistSelectionSection extends Component {

  render() {
    return (
      <SpecialistSelectionSectionContainer>
        <img src={SpecialistSelectionSectionImg} alt="" />
        <Heading size="2.8" mobile={'34'} lheight={'34'} >
          Выбрать специалиста
        </Heading>
        <ParagraphPX size="18" lh={'24'}>
          Оставьте номер мобильного — мы свяжемся с вами и, если вы позволите, поможем заботиться о близком вам человеке
        </ParagraphPX>
        <SendNumber page = {this.props.page} color={'#edebe3'}/>        
        <InfoWrap>
          <Heading size="1.8" mobile={'28'} lheight={'28'}>
            Что произойдет дальше?
          </Heading>
          <div className="content">
            <InfoWrapItem>
              <CircleNumber>1</CircleNumber>
              <ParagraphPX size="14" lh='18'>Сначала позвонит менеджер, который уточнит детали, и вы запланируете встречу с экспертом.</ParagraphPX>
            </InfoWrapItem>
            <InfoWrapItem>
              <CircleNumber>2</CircleNumber>
              <ParagraphPX size="14" lh='18'>Эксперт встретится с вами и вашим родственником, зафиксирует пожелания и наблюдения. На основе этого мы подберем и предложим подходящих специалистов.</ParagraphPX>
            </InfoWrapItem>
            <InfoWrapItem>
              <CircleNumber>3</CircleNumber>
              <ParagraphPX size="14" lh='18'>Вы выберете того, с кем хотите поговорить лично, и примете решение после встречи.</ParagraphPX>
            </InfoWrapItem>
          </div>
        </InfoWrap>
      </SpecialistSelectionSectionContainer>
    )
  }
}

const CircleNumber = styled.span`
  min-width: 24px;
  max-width: 24px;
  min-height: 24px;
  max-height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #edebe3;
  line-height: 24px;
  color: #2e404f;
  font-size: 15px;
  position: relative;
  top: 3px;
`

const InfoWrapItem = styled.div`
  display: flex;
  text-align: left !important;
  width: 30%;
  p {
    color: #edebe3;
    font-weight: 300;
    line-height: 18px;
    max-width: 265px !important;
    margin-left: 10px !important;
  }
  @media(max-width: 768px) {
    width: auto;
    margin: 0 auto;
    margin-bottom: 30px;
  }
`

const InfoWrap = styled.div`
  text-align: center;
  max-width: 900px;
  width: 100%;
  background-color: rgba(34, 34, 34, 0.2);
  border-radius: 10px;
  padding-top: 37px;
  padding-bottom: 53px;
  margin: 0 auto;
  margin-top: 34px;
  .content {
    display: flex;
    margin-top: 20px;
    justify-content: space-around;
    padding: 0 30px;
    @media(max-width: 768px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    @media(max-width: 425px) {
      padding: 0 10px;
    }
  }
  @media(max-width: 376px) {
    h1 {
      padding: 0 20px;
    }
  }
`

const SpecialistSelectionSectionContainer = styled.div`
  padding-top: 42px;
  padding-bottom: 20px;
  background-color: #3d405b;
  text-align: center;
  img {
    margin-bottom: 24px;
    max-height: 180px;
  }
  h1 {
    margin-bottom: 20px;
    color: #edebe3;
  }
  p {
    max-width: 480px;
    color: #edebe3;
    margin: 0 auto;
    font-weight: 200 !important;
  }

  @media(max-width: 480px) {
    img {
      width: 95%;
    }
  }
  @media(max-width: 376px) {
    img {
      width: 80%;
    }
  }
`

export default SpecialistSelectionSection


