import React from 'react';
import styled from 'styled-components';

//Components
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';

//Images
import Photos from '../../../img/specialist_photo1.png';

const SpecialistsSections = props => {
  return (
    <Wrap>
      <div className="heading">
        <Heading size="2.8" mobile={'34'} lheight={'34'}>Знакомьтесь</Heading>
        <Paragraph size="1" mobile={'18'} lheight={'24'}>С нами работают хорошие профессиональные люди, которые хотят сделать жизнь других лучше</Paragraph>
      </div>
      <SpecialistsList>
        <SpecialistsListItem>
          <div className="content">
            <Heading size="1.6" mobile={'28'} lheight={'30'}>
              Эксперт по уходу
            </Heading>
            <Paragraph size="0.9" mobile={'16'} lheight={''}>
              Медицинское образование и очень много опыта работы. Эксперт знакомится с семьей и подопечными и курирует специалистов.
            </Paragraph>
          </div>
            <img src={Photos} alt="" />
        </SpecialistsListItem>
        <SpecialistsListItem>
          <div className="content">
            <Heading size="1.6" mobile={'28'} lheight={'30'}>
              Специалист по уходу
            </Heading>
            <Paragraph size="0.9" mobile={'16'} lheight={''}>
              Профессионал с развитой эмпатией, опыт ухода за подопечными с разными заболеваниями. Закончил нашу Академию. Работает по составленной экспертом программе
            </Paragraph>
          </div>
            <img src={Photos} alt="" />
        </SpecialistsListItem>
        <SpecialistsListItem>
          <div className="content">
            <Heading size="1.6" mobile={'28'} lheight={'30'}>
              Специалист службы помощи
            </Heading>
            <Paragraph size="0.9" mobile={'16'} lheight={''}>
              Отвечает на ваши вопросы в чате и по телефону
            </Paragraph>
          </div>
            <img src={Photos} alt="" />
        </SpecialistsListItem>
      </SpecialistsList>
    </Wrap>
  )
}

const SpecialistsListItem = styled.div`
  background-color: #fff;
  box-shadow: 0 3px 3px rgba(69, 83, 99, 0.2);
  border-radius: 8px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 5px;
  min-width: 300px;
  max-width: 300px;
  margin-bottom: 20px;
  img {
    width: 100%;
    max-width: 300px;
  }
  .content {
    padding: 21px 17px;
    padding-bottom: 0;
    h1 {
      line-height: 28px !important;
      margin-bottom: 10px;
    }
  }

  @media(max-width: 768px) {
    width: 60%;
    margin-bottom: 20px;
  }
  @media(max-width: 480px) {
    width: 95%;
  }
`

const SpecialistsList = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;

  @media(max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }
`

const Wrap = styled.div`
  padding-top: 42px;
  padding-bottom: 23px;
  background-color: #edebe3;
  
  .heading {
    text-align: center;
    p {
      max-width: 400px;
      margin-top: 10px;
      margin: auto;
    }
    h1 {
      line-height: 28px !important;
      margin-bottom: 10px;
    }
  }
`

export default SpecialistsSections


