import React, {Component} from 'react';
import styled from 'styled-components';
import InputMask from 'react-input-mask';

export default class NumberInput extends Component {
  state = {
    focus: false
  }
  render() {
    return (
      <InputMask 
        mask="+7 (999) 999-99-99" 
        maskChar=" " 
        placeholder={this.props.placeholder}
        value={this.props.value}
        onChange={this.props.savePhone}
        onFocus={ () => this.setState({ focus: true })}
        onBlur={ () => this.setState({ focus: false })}
      >
       {(inputprops) => <StyledInput  {...inputprops} valid={this.props.valid} focus={this.state.focus}/>}
      </InputMask>
    )
  }
}

const StyledInput = styled.input`
  background-color: ${ props => props.valid ? '#fff' : '#ffeded'};
  border-radius: 4px;
  border: ${ props => props.valid ? ( props.focus ? 1 : 0) : 1 }px solid #ff6b6b ;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.06);
  outline: none;
  padding: 12.5px 20px;
  width: 260px;
  max-height: 46px;
  box-sizing: border-box;
  font-size: 22px;
  color: #222;
  &::placeholder {
    color: #9b9b9b;
    font-size: 22px;
  }
`


