import React from 'react';
import styled from 'styled-components';

const Button = props => {
return ( 
    <StyledButton 
      onClick={props.click}
      {...props}
    >
      {props.children}
    </StyledButton> 
  )
}
// font-size: 22px; ${ props => (props.font ) || 1}em
const StyledButton = styled.button`
  border: none;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.06);
  border-radius: 4px;
  background-color: #ff6053;
  color: #fff;
  font-size: 22px; 
  cursor: pointer;
  padding: 10px 20px;
  outline: none;
  font-family: "Alegreya Sans";
  box-sizing: border-box;
  max-height: 46px;
  transition: ease 0.5s all;
  &:hover {
    background-color: #c74c42;
  }
`

export default Button


