import React, { Component } from 'react';
import TagManager from 'react-gtm-module';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import styled from 'styled-components';
import SpecialistSelectionSection from '../../common/Sections/SpecialistSelectionSection';
import Section from '../../common/Sections/FaqSection/index';
import { faqData } from '../../data/faq_data';

const tagManagerArgs = {
  dataLayer: {
      userId: '001',
      userProject: 'project',
      page: 'faq'
  },
  dataLayerName: 'PageDataLayer'
}

class FaqPage extends Component {
  componentDidMount() {
    TagManager.dataLayer(tagManagerArgs);
    document.title = 'Служба помощи';
    window.scrollTo(0, 0);

    const { getFaqs } = this.props;

    getFaqs('categories')
  }
  render() {
    const { faq_list } = this.props;

    return (
      <div>
        <Header />

        <Background>
          <TitleWrap>
            <Content>
              <Title size='2.9' weight='500' >Служба помощи</Title>
            </Content>
          </TitleWrap>

          <Content>
            {
              faq_list.map((item, i) => {
                return (
                  <Section data={item} key={i} />
                )
              })
            }
          </Content>
        </Background>

        <SpecialistSelectionSection page='Faq_Main_Page'/>
        <Footer />
      </div>
    )
  }
}

const Background = styled.div`
    background-color: #edebe3;
    width: 100%;
    @media(max-width: 1000px) {
        padding-top: 40px;
    }
`
const Title = styled.h1`
    font-family: "Alegreya Sans";
    font-size: ${ props => props.size}em;
    font-weight: ${ props => props.weight};
    margin: 0 0;
    margin-bottom: ${ props => props.bottom || 0}px;
    height: 100%;
    display: flex;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
    @media(max-width: 960px) {
        justify-content: center
    }
    @media(max-width: 768px) {
        font-weight: 500;
    }
    @media(max-width: 376px) {
      font-size: 34px;
      line-height: 34px;
    }
`

const Content = styled.div`
    padding-left: 4%;
    padding-right: 4%;
    max-width: 1024px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0 auto;
`

const TitleWrap = styled.div`
    height: 100px;
    width: 100%;
    border-bottom: 2px solid rgba(34,34,34,0.2);
    margin-bottom: 30px;
    @media(max-width: 768px) {
        margin-bottom: 15px;
    }
    @media(max-width: 376px) {
      padding: 35px 0;
    }
`


export default FaqPage;





