import {
  REQUEST_FAQ,
  SUCCES_FAQ,
  ERROR_FAQ
} from '../constants/faq.js';

let initialState = {
  faq_list: [],
  errors: null,
  isLoading: true
}

export default function faqStore( state = initialState, action ) {
  switch (action.type) {
    case REQUEST_FAQ: 
      return {
        ...state,
        Error: null,
        isLoading: true
      }
    case SUCCES_FAQ:
      return {
        ...state,
        isLoading: false,
        faq_list: action.payload,
        errors: action.payload.Error || null
      }
    case ERROR_FAQ:
      return {
        ...state,
        isLoading: false,
        errors: action.payload
      }
    default:
      return state;
  }
}



