import React from 'react';
import styled from 'styled-components';

const Heading = (props) => {
  return (
  <StyledHeading 
    {...props} 
    size={props.size}
    mobile={props.mobile}
    lh={props.lheight}
  >
    {props.children}
  </StyledHeading>)
}

const StyledHeading = styled.h1`
  font-size: ${props => props.size}em;
  font-family: "Alegreya";
  color: #222;
  font-weight: 500;
  margin: 0;
  line-height: 40px;
  @media(max-width: 376px) {
    font-size: ${ props => props.mobile }px;
    line-height: ${ props => props.lh }px;
    font-weight: 500;
  }
`

export default Heading


