import React, { Component } from 'react';
import styled from 'styled-components';
import Avatar from 'react-avatar';

//Slick slider
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

//Images
import SliderImgLeft from '../../../img/slider_arrow_left.svg';
import SliderImgRight from '../../../img/slider_arrow_right.svg';

//Components
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';

const settings = {
  dots: false,
  infinite: true,
  slidesToShow: 1,
  slidesToScroll: 1,
  arrows: true,
  centerMode: true,
  variableWidth: true,
  responsive: [
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 3,
        infinite: true
      }
    },
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false
      }
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false
      }
    }
  ]
};

const ReviewsSliderItem = (props) => {
  const { text, author } = props;

  return (
    <SliderItem>
      <StyledParagraph>
        {text}
      </StyledParagraph>
      <div className="avatar_name">
        <SliderAvatar
          src="https://24smi.org/public/media/235x307/celebrity/2015/10/29/1446106852-stiv-dzhobs.jpg"
          round={true}
          size={70} />
      </div>
      <SliderItemBottom>
        <Heading size="1">
          {author}
        </Heading>
      </SliderItemBottom>
    </SliderItem>
  )
}

const StyledParagraph = styled.p`
  font-size: 16px;
  font-family: "Alegreya Sans";
  color: #000;
  letter-spacing: 0;
  line-height: 22px;
  font-weight: 400;
  margin: 0;
`

class ReviewsSection extends Component {
  render() {
    return (
      <ReviewsSectionWrap>
        <Heading size="2.8" mobile={'34'} lheight={'34'}>
          Отзывы клиентов
        </Heading>
        <Slider {...settings}>
          <ReviewsSliderItem
            text="Клинеры конечно всегда разные приходят, но мне с ребятами из Qlean всегда везёт. (...) Всё делают качественно, на совесть. Нравятся средства, которые используются при уборке — экологичные, т.е. ребенку меньше вреда, при этом всё блестит. "
            author="Валентина Петровна"
          />
          <ReviewsSliderItem
            text="Операторы по телефону, служба помощи в Телеграме, сам клинер — все были безгранично любезны, полезны и милы. Редко такое встречаю. Это определенно новая планка для сферы услуг в России."
            author="Валентина Петровна"
          />
          <ReviewsSliderItem
            text="Девушки пунктуальные. Во время уборки я ухожу из квартиры, окончание уборки может не совпасть с предполагаемым временем, поэтому клинер всегда звонит и предупреждает, когда можно возвращаться. Качество меня устраивает — всё аккуратно."
            author="Валентина Петровна"
          />
          <ReviewsSliderItem
            text="Клинеры конечно всегда разные приходят, но мне с ребятами из Qlean всегда везёт. (...) Всё делают качественно, на совесть. Нравятся средства, которые используются при уборке — экологичные, т.е. ребенку меньше вреда, при этом всё блестит. "
            author="Валентина Петровна"
          />
          <ReviewsSliderItem
            text="Операторы по телефону, служба помощи в Телеграме, сам клинер — все были безгранично любезны, полезны и милы. Редко такое встречаю. Это определенно новая планка для сферы услуг в России."
            author="Валентина Петровна"
          />
          <ReviewsSliderItem
            text="Девушки пунктуальные. Во время уборки я ухожу из квартиры, окончание уборки может не совпасть с предполагаемым временем, поэтому клинер всегда звонит и предупреждает, когда можно возвращаться. Качество меня устраивает — всё аккуратно."
            author="Валентина Петровна"
          />
        </Slider>
      </ReviewsSectionWrap>
    )
  }
}

const SliderAvatar = styled(Avatar)`
  margin: auto;
  position: relative;
  display: block;
  width: 95%;
  margin: auto;
`

const SliderItemBottom = styled.div`
  width: 100%;
  background-color: rgba(237, 235, 227, 0.4);
  padding: 24px 0;
  text-align: center;
  h1 {
    font-weight: 700;
    margin-top: 12px;
  }
`

const SliderItem = styled.div`
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 3px 3px rgba(69, 83, 99, 0.2);
  /* padding: 20px; */
  /* box-sizing: border-box; */
  padding-top: 10px;
  width: 280px;
  margin-left: 6px;
  margin-right: 6px;
  
  text-align: left !important;
  p {
    margin: 20px;
    margin-bottom: 30px;
  }
  img {
    text-align: center;
  }
  .avatar_name {
    display: flex;
    justify-content: center;
    width: 100%;
    position: relative;
    top: 35px;
    margin-top: -35px;
  }
`

const ReviewsSectionWrap = styled.div`
  padding: 42px 0;
  background-color: #edebe3;
  text-align: center;
  div {
    outline: none;
  }
  .slick-slider {
    margin-top: 30px;
    .slick-arrow {
      width: 40px;
      height: 80px;
      border: 1px solid #222;
      border-radius: 4px;
      box-shadow: 0 3px 3px 0 rgba(69, 83, 99, 0.2);
      background-repeat: no-repeat;
      background-position: center;
      background-color: #fff;
      font-size: 0;
      z-index: 100;
      outline: none;
      box-sizing: border-box;
      opacity: 1 !important;
      &:before {
        content: '';
        opacity: 1 !important;
      }
    }
    .slick-prev {
      left: 20px;
      background-image: url(${SliderImgLeft});
      
    }
    .slick-next {
      right: 20px;
      background-image: url(${SliderImgRight});
      transform: scale(-1, 1);
      top: 40%;     
    }
    .slick-track {
      padding-bottom: 10px;
      display: flex;
      align-items: center;
    }
  }
`

export default ReviewsSection


