import React from 'react';
import { connect } from 'react-redux';
import FaqArticlePage from '../components/pages/FaqArticlePage';
import { getFaqs } from '../store/actions/faq.js';

const FaqArticlePageContainer = props => {
  const {
    match: {
      params : {
        section,
        sub_section,
        article
      }
    }
  } = props;

  const sectionData = props.faq_list && props.faq_list.filter(i => {
    if(i.slug === section) return i
  })[0]


  const subSectionData = sectionData && sectionData.cats.filter(i => {
    if(i.slug === sub_section ) return i
  })[0]

  const contentData = subSectionData && subSectionData.posts.filter(i => {
    if(i.post_name === article) return i
  })[0]

  return (
    <FaqArticlePage 
      {...props} 
      sectionData={sectionData || null} 
      subSectionData={subSectionData || null}
      contentData={contentData || null} />
  )
}

const mapStateToProps = state => {
  return {
    faq_list: state.faqStore.faq_list,
    isLoading: state.faqStore.isLoading,
    errors: state.faqStore.errors
  }
}


const mapDispatchToProps = dispatch => {
  return {
    getFaqs: (value) => dispatch(getFaqs(value))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(FaqArticlePageContainer)



