import React, { Component } from 'react';
import TagManager from 'react-gtm-module';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import ImportantDocumentsSection from '../../common/Sections/ImportantDocumentsSection';
import PublicOfferContractSection from '../../common/Sections/PublicOfferContractSection';
import TermsOfUseSection from '../../common/Sections/TermsOfUseSection';
import AddressesAndRequisitesSection from '../../common/Sections/AddressesAndRequisitesSection';

const tagManagerArgs = {
  dataLayer: {
      userId: '001',
      userProject: 'project',
      page: 'documents'
  },
  dataLayerName: 'PageDataLayer'
}

class DocumentsPage extends Component {
  componentDidMount() {
    TagManager.dataLayer(tagManagerArgs);
    document.title = 'Документы';
    window.scrollTo(0, 0);
  }
  render() {
    return (
      <div>
        <Header />
        <ImportantDocumentsSection />
        <PublicOfferContractSection />
        <TermsOfUseSection />
        <AddressesAndRequisitesSection />
        <Footer />
      </div>
    )
  }
}

export default DocumentsPage


