import React from 'react';
import styled from 'styled-components';

const ParagraphPX = (props) => {
  return <StyledParagraph {...props} size={props.size}>{props.children}</StyledParagraph>
}

const StyledParagraph = styled.p`
  font-size: ${props => props.size}px;
  font-family: "Alegreya Sans";
  line-height: ${props => props.lh || props.size }px;
  color: #222;
  font-weight: 400;
  margin: 0;
`

export default ParagraphPX;


