import { connect } from 'react-redux';
import FaqPage from '../components/pages/FaqPage';
import { getFaqs } from '../store/actions/faq.js';

const mapStateToProps = state => {
  return {
    faq_list: state.faqStore.faq_list,
    isLoading: state.faqStore.isLoading,
    errors: state.faqStore.errors
  }
}


const mapDispatchToProps = dispatch => {
  return {
    getFaqs: (value) => dispatch(getFaqs(value))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(FaqPage)



