import React, { Component } from 'react';
import { connect } from 'react-redux';
import FaqQuestionsPage from '../components/pages/FaqQuestionsPage';
import { getFaqs } from '../store/actions/faq.js';

class FaqQuestionsPageContainer extends Component {
  render() {
    const {
      match: {
        params: {
          section,
          sub_section
        }
      }
    } = this.props;

    const sectionData = this.props.faq_list.length && this.props.faq_list.filter(i => {
      if (i.slug === section) return i
    })[0]

    const subSectionData = sectionData && sectionData.cats.filter(i => {
      if (i.slug === sub_section) return i
    })[0]

    return (
      <FaqQuestionsPage
        {...this.props}
        sectionData={sectionData}
        subSectionData={subSectionData} />
    )
  }
}

const mapStateToProps = state => {
  return {
    faq_list: state.faqStore.faq_list,
    isLoading: state.faqStore.isLoading,
    errors: state.faqStore.errors
  }
}


const mapDispatchToProps = dispatch => {
  return {
    getFaqs: (value) => dispatch(getFaqs(value))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(FaqQuestionsPageContainer)



