const ListItem = styled.div`
  display: flex;
  margin-bottom: 30px;
  i {
    font-size: 2.2em;
    color: #ff6053;
    position: relative;
    top: -10px;
    font-style: 700;
    @media(max-width: 376px) {
      font-size: 25px;
      font-style: 700;
      top: 2px;
    }
  }
  .content {
    margin-left: 13px;
    display: flex;
    flex-direction: column;
    text-align: left !important;
    h1 {
      line-height: 28px;
    }
    p {
      margin: 0;
      margin-top: 10px;
      max-width: 340px;
    }
  }
  @media(max-width: 376px) {
    margin-bottom: 20px;
  }
`

const ListWrap = styled.div`
  display: flex;
  justify-content: space-around;
  max-width: 1024px;
  margin: 0 auto;
  margin-top: 40px;

  @media(min-width: 1256px) {
    justify-content: space-around;
  }

  @media(max-width: 768px) {
    flex-direction: column;
    width: 60%;
    margin: auto;
    margin-top: 40px;
  }

  @media(max-width: 480px) {
    width: 98%;
  }

`

const Wrap = styled.div`
  padding-top: 37px;
  padding-bottom: 88px;
  width: 80%;
  margin: auto;
  text-align: center;
  p {
    max-width: 380px;
    margin: auto;
    margin-top: 15px;
  }
  h1 {
    line-height: 33px;
  }
  @media(max-width: 376px) {
    width: 90%;
  }
  
`

export default YouCanBeCalmSection


