import React from 'react';
import styled from 'styled-components';

//Components
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';
import Button from '../../Button';
import { Link } from 'react-router-dom';

const HelpDeskSection = props => {
  return (
    <Wrap>
      <Heading size="2.8" mobile={'34'} lheight={'34'}>
        Служба помощи
      </Heading>
      <Paragraph size="1" mobile={'18'} lheight={'24'}>
        Если у вас остались вопросы, на которые вы не нашли ответов на сайте, позвоните или напишите нам
      </Paragraph>
      <Link to="/faq" >
        <Button> Вопросы и ответы </Button>
      </Link>
    </Wrap>
  )
}



const Wrap = styled.div`
  padding-top: 47px;
  padding-bottom: 53px;
  text-align: center;
  h1 {
    margin-bottom: 20px;
  }
  p {
    max-width: 450px;
    margin: auto;
  }
  button {
    margin-top: 20px;
    font-size: 1.2em;
    max-height: 46px;
    font-weight: 300;
    width: 260px;
  }
  @media(max-width: 600px) {
    width: 90%;
    margin: auto;
  }
`

export default HelpDeskSection


