import React, { Component } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

//Components
import Heading from '../Heading';
import Paragraph from '../Paragraph';
import Icon from '../Icon';

//Images
import Logo from '../../img/logo.svg'
import LogoTitleWhite from '../../img/LogoTitleWhite.svg';

class Footer extends Component {
  render() {
    return (
        <BackgroundColor>
          <Wrap>
          <LogoWrap>
            <LogoCircle>
              <img alt="logo" src={Logo} />
            </LogoCircle>
            <img src={LogoTitleWhite} alt='logo title' className='logo-title'/>
            <Paragraph size="0.7" mobile={'12'} lheight={'12'} >
            © 2018  ООО <br /> «Надежные исполнители» <br /> ОГРН7373737373
            </Paragraph>
          </LogoWrap>
          <MenuWrap>
            <Heading className="footer_menu_header" size="0.9" mobile={'14'} lheight={'16'} >
              Организация ухода
            </Heading>
            <div className="menu_list">
              <FooterMenuLink to="/uslugi">
                Услуги
              </FooterMenuLink>
              <FooterMenuLink to="/specialists">
                Специалисты
              </FooterMenuLink>
              <FooterMenuLink to="/security">
                Безопасность
              </FooterMenuLink>
              <FooterMenuLink to="/faq">
                Служба помощи
              </FooterMenuLink>
              <FooterMenuLink to="/documents">
                Документы
              </FooterMenuLink>
              <FooterMenuLink to="/">
                О компании
              </FooterMenuLink>
            </div>
          </MenuWrap>
          <ContactsWrap>
            <Heading className="footer_menu_header" size="0.9" mobile={'14'} lheight={'16'}>
              Для связи
            </Heading>
            <PhoneNumber href="tel:+7(495)120-19-90">
              +7 (495) 565-36-50
            </PhoneNumber>
            <SocialLink href="https://www.facebook.com/">
              <Icon icon="facebook-messenger" />
              Facebook
            </SocialLink>
            <SocialLink href="https://vk.com/">
              <Icon icon="vk" />
              Vkontakte
            </SocialLink>
            <SocialLink href="https://telegram.org/">
              <Icon icon="telegram" />
              Telegram
            </SocialLink>
          </ContactsWrap>
        </Wrap>
      </BackgroundColor>
    )
  }
}

const BackgroundColor = styled.div`
  background-color: #222222;
`

const SocialLink = styled.a`
  font-size: 1em;
  color: #edebe3;
  display: flex;
  align-items: center;
  text-decoration: none;
  font-weight: 300;
  margin-top: 14px;
  transition: ease 0.5s all;
  line-height: 1em;
  i {
    color: #edebe3;
    font-size: 1.5em;
    margin-right: 10px;
    transition: ease 0.5s all;
  }
  &:hover {
    color: #ff6053;
    i {
      color: #ff6053;
    }
  }
`

const PhoneNumber = styled.a`
  /* font-style: italic; */
  font-size: 1.2em;
  color: #edebe3;
  font-weight: 500;
  text-decoration: none;
  transition: ease 0.5s all;
  &:hover {
    color: #ff6053; 
  }
`

const FooterMenuLink = styled(Link)`
  font-size: 1.3em;
  color: #edebe3;
  font-weight: 500;
  padding: 2px 0;
  text-decoration: none;
  transition: ease 0.5s all;
  font-family: "Alegreya Sans";
  &:hover, .active {
    color: #ff6053;
  }
  @media(max-width: 768px) {
    color: #fff;
  }
  @media(max-width: 376px) {
    font-size: 22px;
    font-weight: 500;
  }
`

const LogoCircle = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #edebe3;
  border-radius: 50%;
  min-width: 62px;
  max-width: 62px;
  min-height: 62px;
  max-height: 62px;

  @media(max-width: 652px) {
    margin-top: 60px;
  }
`

const ContactsWrap = styled.div`
  @media(max-width: 652px) {
    order: 2;
    h1 {
      margin-bottom: 10px;
    }
  }
`

const MenuWrap = styled.div`
  .menu_list {
    display: flex;
    flex-direction: column;
  }
  @media(max-width: 652px) {
    order: 1;
    margin-bottom: 20px;
    h1 {
      margin-bottom: 10px;
    }
  }
`

const LogoWrap = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  h1 {
    color: #edebe3;
    margin-top: 64px;
  }
  p {
    color: #fff;
    opacity: 0.5;
    margin-top: 12px;
  }
  .logo-title {
    margin-top: 50px;
  }
  @media(max-width: 652px) {
    align-items: flex-start;
    order: 3;
    h1 {
      margin-top: 25px;
    }
    .logo-title {
      margin-top: 30px;
    }
  }
  /* @media(max-width: 376px) {
    p {
      display: block;
      width: 60%;
    }
  } */
`

const Wrap = styled.footer`
  padding-top: 32px;
  padding-bottom: 50px;
  color: #edebe3;
  display: flex;
  justify-content: space-between;
  max-width: 650px;
  margin: 0 auto;
  .footer_menu_header {
    color: #fff;
    opacity: 0.5;
    font-weight: 300;
  }

  @media(max-width: 652px) {
    flex-direction: column;
    margin-left: 5%;
  }
`

export default Footer


