import React, { Component } from 'react';
import styled from 'styled-components';
import TagManager from 'react-gtm-module';

//Images
import SecurityPageImg from '../../img/SecurityPageImg.svg';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import SpecialistSelectionSection from '../../common/Sections/SpecialistSelectionSection';
import QuestionsAndAnswersSection from '../../common/Sections/QuestionsAndAnswersSection';
import QualityAssuranceSection from '../../common/Sections/QualityAssuranceSection';
import InsuranceResponsibilitySection from '../../common/Sections/InsuranceResponsibilitySection';
import VerificationOfSpecialists from '../../common/Sections/VerificationOfSpecialists';
import HelpDeskSection from '../../common/Sections/HelpDeskSection';
import Paragraph from '../../common/Paragraph';
import Heading from '../../common/Heading';
import SendNumber from '../../common/SendNumber/index';

const tagManagerArgs = {
  dataLayer: {
      userId: '001',
      userProject: 'project',
      page: 'security'
  },
  dataLayerName: 'PageDataLayer'
}

class SecurityPage extends Component {

  componentDidMount() {
    TagManager.dataLayer(tagManagerArgs);
    document.title = 'Безопасность';
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div>
        <Header />
        <SecurityPageHeading>
          <img src={SecurityPageImg} alt="" />
          <Heading size="2.9" mobile={'34'} lheight={'34'}>
            Безопасность
          </Heading>
          <Paragraph size="1.35" mobile={'18'} lheight={'22'}>
            Одна из главных наших задач — забота о&nbsp;безопасности подопечных и специалистов.
          </Paragraph>
          <SecurityPageButtonsWrap>
            <SendNumber page='security_page'/>
          </SecurityPageButtonsWrap>
        </SecurityPageHeading>
        <QualityAssuranceSection />
        <InsuranceResponsibilitySection />
        <VerificationOfSpecialists />
        <HelpDeskSection />
        <SpecialistSelectionSection page='security_page'/>
        <QuestionsAndAnswersSection />
        <Footer />
      </div>
    )
  }
}

const SecurityPageButtonsWrap = styled.div`
  display: flex;
  align-items: center;
  /* margin-top: 20px; */
  button {
    margin-left: 10px;
  }

  @media(max-width: 530px) {
    flex-direction: column;
    button {
      margin: 0 auto;
      margin-top: 12px;
      width: 100%;
    }
    input: {
      width: 100%;
    }
  }
`

const SecurityPageHeading = styled.div`
  background-color: #edebe3;
  padding: 50px 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  img {
    max-height: 280px;
  }
  p {
    max-width: 550px;
    margin-top: 20px;
  }
  h1 {
    margin: 4px 0;
  }

  @media(max-width: 768px) {
    margin-top: 20px;
  }

  @media(max-width: 480px) {
    img {
      width: 100%;
    }
  }
`

export default SecurityPage


