import React from 'react';
import styled from 'styled-components';
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';


//Section cards images
import clockIcon from '../../../img/CostOfServicesSectionImg/clock.svg';
import calendarIcon from '../../../img/CostOfServicesSectionImg/calendar.svg';
import tableClockIcon from '../../../img/CostOfServicesSectionImg/table_clock.svg';

const CostOfServicesSection = props => {
  return (
    <Wrap>
      <div className="heading">
        <Heading size="2.8" mobile={'34'} lheight={'34'}>
          Стоимость услуг
        </Heading>
        <Paragraph size="1" mobile={'18'} lheight={'24'} >
          Мы верим, что каждая работа должна оплачиваться соответственно опыту и экспертизе, 
          поэтому наши специалисты могут устанавливать собственные тарифы. По умолчанию, 
          мы рекомендуем им работать по базовому тарифу 
          (<em>в стоимость уже включена наша комиссия 25%</em>). 
        </Paragraph>
      </div>
      <CardsWrap>
        <CardItem>
          <div>
            <img src={clockIcon} title="" className='img_height' alt='clock'/>
          </div>
          <Heading size="1.6" mobile={'28'} lheight={'30'}>
            Разовая помощь
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={''}>
            Например, подменить вас на&nbsp;время отпуска
          </Paragraph>
          <CardPrice>
            <Paragraph size="1.5" mobile={'24'} lheight={'24'}>
            от <span>3600 ₽</span> <br /> в&nbsp;неделю
            </Paragraph>
          </CardPrice>
        </CardItem>
        <CardItem>
          <div>
            <img src={tableClockIcon} title=""  alt='table clock'/>
          </div>
          <Heading size="1.6" mobile={'28'} lheight={'30'} >
            Постоянный уход
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={''} >
            Регулярная помощь по&nbsp;дому, уколы и процедуры по&nbsp;расписанию
          </Paragraph>
          <CardPrice>
            <Paragraph size="1.5" mobile={''} lheight={''} >
            от <span>7500 ₽</span> <br /> в&nbsp;неделю
            </Paragraph>
          </CardPrice>
        </CardItem>
        <CardItem>
          <div>
            <img src={calendarIcon} title="" alt='calendar' />
          </div>
          <Heading size="1.6" mobile={'28'} lheight={'30'}>
            Постоянный уход <br /> с&nbsp;проживанием
          </Heading>
          <Paragraph size="1" mobile={'18'} lheight={''}>
            Круглосуточное внимание
          </Paragraph>
          <CardPrice>
            <Paragraph size="1.5" mobile={''} lheight={''}>
            от <span>9200 ₽</span> <br /> в&nbsp;неделю
            </Paragraph>
          </CardPrice>
        </CardItem>
      </CardsWrap>
    </Wrap>
  )
}

const CardPrice = styled.div`
  p {
    font-style: italic;
    font-weight: 500 !important;
    font-family: "Alegreya";
    span {
      font-size: 2em;
      color: #ff4c00;
    } 
  }
`

const CardItem = styled.div`
  box-shadow: 0 3px 3px rgba(69, 83, 99, 0.2);
  border-radius: 8px;
  background-color: #ffffff;
  padding: 0 1.3%;
  box-sizing: border-box;
  padding-top: 26px;
  padding-bottom: 20px;
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 5px;
  margin-bottom: 20px;
  min-width: 290px;
  max-width: 300px;
  max-height: 450px;
  div img {
    max-height: 131px;
    max-width: 190px;
    margin-bottom: 30px;
  }
  div .img_height {
    max-height: 170px;
    margin-bottom: 0;
  }
  h1 {
    margin-top: 22px;
    line-height: 28px;
  }
  p {
    margin-top: 7px;
  }

   @media(max-width: 768px) {
     width: 70%;
     margin-bottom: 20px;
   }

   @media(max-width: 480px) {
     width: 90%;
   }
`

const CardsWrap = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-top: 40px;
  max-width: 950px;
  @media(max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }
`

const Wrap = styled.div`
  padding: 0 10%;
  padding-top: 47px;
  padding-bottom: 45px;
  background-color: #edebe3;
  text-align: center;
  .heading {
    p {
      margin: auto;
      margin-top: 15px;
      max-width: 500px;
      @media(max-width: 376px) {
        text-align: left;
      }
    }
  }
  @media(max-width: 1024px) {
    padding: 0 5%;
    padding-top: 47px;
    padding-bottom: 45px;
  }


`

export default CostOfServicesSection


