import React, { Component } from 'react';
import styled from 'styled-components';
import Heading from '../../Heading';
import Icon from '../../Icon';
// import Paragrapg from '../../Paragraph';
import Collapse, { Panel } from 'rc-collapse';
import 'rc-collapse/assets/index.css';

const HeaderCollapse = props => {
  return (
    <StyleHeaderCollapse>
      <Icon icon="chevron-down" />
      <Icon icon="chevron-right" />
      {props.title}
    </StyleHeaderCollapse>
  )
}

class QuestionsAndAnswersSection extends Component {
  render() {
    return (
      <Wrap>
        <Heading size="2.9" mobile={'34'} lheight={'34'} >
          Вопросы и ответы
        </Heading>
        <div className="accordion">
        <Collapse defaultActiveKey={"0"} accordion={false}>
          <Panel 
            showArrow={false}
            header={<HeaderCollapse title="Сколько стоит подбор специалиста по уходу?"/>} 
            headerClass="my-header-class">
            В удобнное время к вам приедет наша пробная сиделка, объяснит вам как работают внуки, и поможет составить индивидуальную программу специально под вам и ваших близких
          </Panel>
          <Panel 
            showArrow={false}
            header={<HeaderCollapse title="Сколько стоит заменить специалиста по уходу?"/>} 
            headerClass="my-header-class">
            В удобнное время к вам приедет наша пробная сиделка, объяснит вам как работают внуки, и поможет составить индивидуальную программу специально под вам и ваших близких
          </Panel>
          <Panel 
            showArrow={false}
            header={<HeaderCollapse title="Если что-то случится, ваш эксперт будет на связи?"/>} 
            headerClass="my-header-class">
            В удобнное время к вам приедет наша пробная сиделка, объяснит вам как работают внуки, и поможет составить индивидуальную программу специально под вам и ваших близких
          </Panel>
          <Panel 
            showArrow={false}
            header={<HeaderCollapse title="Вы работаете официально? Есть ли у вас все необходимые документы?"/>} 
            headerClass="my-header-class">
            В удобнное время к вам приедет наша пробная сиделка, объяснит вам как работают внуки, и поможет составить индивидуальную программу специально под вам и ваших близких
          </Panel>
          <Panel 
            showArrow={false}
            header={<HeaderCollapse title="Как быстро вы можете помочь мне с уходом?"/>} 
            headerClass="my-header-class">
            В удобнное время к вам приедет наша пробная сиделка, объяснит вам как работают внуки, и поможет составить индивидуальную программу специально под вам и ваших близких
          </Panel>
          <Panel 
            showArrow={false}
            header={<HeaderCollapse title="Кто такой “эксперт по уходу” и зачем он нужен?"/>} 
            headerClass="my-header-class">
            В удобнное время к вам приедет наша пробная сиделка, объяснит вам как работают внуки, и поможет составить индивидуальную программу специально под вам и ваших близких
          </Panel>
        </Collapse>
        </div>
      </Wrap>
    )
  }
}

const StyleHeaderCollapse = styled.div`
  display: flex;
  align-items: center;
  .mdi-chevron-down {
    display: none !important;
  }
  i {
    color: #ff6053;
    font-size: 1.3em;
  }
`

const Wrap = styled.div`
  max-width: 560px;
  margin: 0 auto;
  width: 100%;
  text-align: center;
  padding-top: 52px;
  padding-bottom: 30px;
  h1 {
    font-family: "Alegreya Sans" !important;
  }
  .accordion {
    margin: auto;
    margin-top: 20px;
    text-align: left;
    .rc-collapse {
      border-left: 0px solid #fff !important;
      border-right: 0px solid #fff !important; 
      border-color: #e2e1d9;
      background: #fff;
      .rc-collapse-item {
        padding: 8px 0;
        .rc-collapse-header {
          @media(max-width: 376px) {
            text-indent: 8px;
            padding-right: 10px;
          }
        }
      }
    }
    .rc-collapse-header {
      background: #fff;
      font-family: "Alegreya Sans";
      font-weight: 500;
      color: #222 !important;
      font-size: 1.05em;
      outline: none;
      line-height: 20px !important;
      height: auto !important;
    }
    .rc-collapse-content {
      color: #222 !important;
      font-size: 1em;
      font-weight: 400;
      font-family: "Alegreya Sans";
      margin-left: 46px;
      @media(max-width: 376px) {
        margin-left: 24px;
      }
    }
    .rc-collapse-item-active {
      .mdi-chevron-right {
        display: none;
      }
      .mdi-chevron-down {
        display: block !important;
      }
    }
    @media(max-width: 768px) {
      width: 90%;
    }
    @media(max-width: 376px) {
      width: 100%;
    }
  }
  @media(max-width: 768px) {
    padding-top: 20px;
  }
`

export default QuestionsAndAnswersSection


