import React, { Component } from 'react';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import styled from 'styled-components';
import SpecialistSelectionSection from '../../common/Sections/SpecialistSelectionSection';
import Column from '../../common//Column/index';
import NavLink from '../../common//NavLink/index';
import arrow from '../../img/link-arrow.svg';
import { faqData } from '../../data/faq_data';
import NoMatch from './NoMatch';

class FaqQuestionsPage extends Component {
  
  componentDidMount() {
    document.title = 'Служба помощи';
    window.scrollTo(0, 0);
    if(!this.props.faq_list.length) this.props.getFaqs();
  }
  render() {
    
    if(this.props.isLoading) {
      return null
    } 
    else if(!this.props.isLoading && (!this.props.sectionData || !this.props.subSectionData)) {
      return <NoMatch />
    }
    else {
      const {
        sectionData,
        subSectionData
      } = this.props;
      return (
        <div>
          <Header />

          <Background>
            <Content>
              <Navigation>
                <NavLink path={'/faq'} name={'Служба помощи'} />
                <Arrow> <img src={arrow} alt='' /> </Arrow>
                <NavLink path={`/faq/${sectionData.slug}`} name={sectionData.name} />
                <Arrow> <img src={arrow} alt='' /> </Arrow>
                <NavLink
                  path={`/faq/${sectionData.slug}/${subSectionData.slug}`}
                  name={subSectionData.name}
                  color={'#9b9b9b'}
                  hide={true}
                />
              </Navigation>
              <Column
                data={subSectionData}
                url={`${sectionData.slug}`}
                width={'100%'}
                all={true}
                font={''}
                boldTitle={true}
              />
            </Content>
          </Background>

          <SpecialistSelectionSection page='Faq_Questions_Page'/>
          <Footer />
        </div>
      )
    }
  }
}

const Arrow = styled.div`
    height: 100%;
    margin: 0 2.5%;
    img {
        width: 8px;
    }
`
const Navigation = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding-top: 10px;
    margin-bottom: 20px;
    @media(max-width: 1000px) {
        margin-bottom: -10px;
    }
`
const Background = styled.div`
    background-color: #edebe3;
    width: 100%;
    @media(max-width: 1000px) {
        padding-top: 30px;
    }
`
const Content = styled.div`
    max-width: 600px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin-left: 60px;
    display: flex;
    flex-direction: column;
    @media(max-width: 680px) {
      margin-left: 20px;
      width: 95%;
    }
    @media(max-width: 600px) {
      margin-left: 20px;
      width: 90%;
    }
`

export default FaqQuestionsPage;





