import React, {Component} from 'react';
import styled from 'styled-components';
import NumberInput from '../NumberInput/index';

export default class SendNumber extends Component {
    state = {
        valid: true,
        show: false,
        number: '',
        focus: false
    }
    
    sendPhoneNumber = () => {
        let number = this.state.number.replace(/\D/g, "") ;
        if(number.length == 11) {
            this.setState({ show: !this.state.show });
            let data = {
                page: this.props.page,
                phone: number
            }
            console.log( 'Page', data );
        } else {
            console.log('Not Valid');
            this.setState({valid: false});
        }

    }
    
    saveNumber = (event) => {
        this.setState({number: event.target.value, valid: true});
    }
  
    render() {
        let message = (
            <MessageError>  
                Кажется, Вы ошиблись при вводе номера телефона 
            </MessageError> 
        )
        return (
            <Wrap>
                {   
                    this.state.show
                        ?
                            <FormWrap width={this.state.show}>
                                <Thanks color={ this.props.color }>
                                    <h3>Спасибо!</h3>
                                    <p>Мы перезвоним вам в течение десяти минут</p>
                                </Thanks>
                            </FormWrap>
                        :
                            <FormWrap valid={true} width={this.state.show}>
                                {
                                    this.state.valid
                                        ? null
                                        : message
                                }
                                <NumberInput 
                                    type="text" 
                                    placeholder="+7 (999) xxx-xx-xx "
                                    value = {this.state.number}
                                    savePhone={this.saveNumber}
                                    valid={this.state.valid}
                                    // typing={this.state.typing}
                                />
                                <Button onClick={ () => this.sendPhoneNumber() }>
                                    Оставить заявку
                                </Button>
                            </FormWrap>
                }
            </Wrap>
        )
    }
}

const Wrap = styled.div`
    margin: 0 auto;
    margin-top: ${ props => props.color ? 10 : 30  }px;
    max-width: 490px;
`

const Button = styled.div`
  width: 220px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.06);
  border-radius: 4px;
  background-color: #ff6053;
  color: #fff;
  font-size: 22px;
  cursor: pointer;
  font-family: "Alegreya Sans";
  box-sizing: border-box;
  height: 46px;
  transition: ease 0.5s all;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
    &:hover {
      background-color: #c74c42;
    }
  @media(max-width: 550px) {
    width: 260px;
    margin-left: 0;
    margin-top: 15px;
  }
`

const Thanks = styled.div`
  h3 {
    margin: 5px 0;
    color: ${ props => props.color ? props.color : '#222'  };
    font-size: 2em;
  }
  p {}
`

const MessageError = styled.p`
    box-sizing: border-box;
    margin: 0 0;
    padding: 10px 20px;
    text-align: start;
    display: flex;
    position: absolute;
    width: 260px;
    background-color: #222;
    color: #fff;
    bottom: 55px;
    left: 0;
    border-radius: 4px;
    font-size: 16px;
    line-height: 18px;
    &:after {
        content: '';
        display: block;
        transform: rotate(45deg);
        width: 18px;
        height: 18px;
        background-color: #222;
        position: absolute;
        top: 47px;
        left: 50%;
    } 
    @media(max-width: 550px) {
        bottom: 112px;
    }
`

const FormWrap = styled.form`
  display: flex;
  justify-content: center;
  position: relative;
  input {
    font-size: 22px;
  }
  button {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 220px;
    margin-left: 10px;
    font-size: 22px;
    max-height: 46px;
  }

  @media(max-width: 550px) {
    width: ${ props => props.width ? '100%' : '260px'};
    margin: 0 auto;
    flex-direction: column;
    align-items: center;
    button {
      margin-top: 20px;
    }
  }
`



